<template>
  <div id="app" class="app-viewport">
    <v-tabs icons-and-text centered color="blue-grey darken-1">
      <v-tabs-slider color="white" style="width:100%;"></v-tabs-slider>
      <v-tab to="/" style="color:white !important;">
        Matchs
        <v-icon color="white">access_time</v-icon>
      </v-tab>
      <v-tab key="1" to="/ligue1" style="color:white !important;" @click="toLigue1">
        Ligue1
        <v-icon key="2" color="white" style="position:relative;left:10px;">start</v-icon>
      </v-tab>
      <v-tab to="/ligue2" style="color:white !important;" @click="toLigue2">
        Ligue2
        <v-icon color="white" style="position:relative;left:10px;">start</v-icon>
      </v-tab>
    </v-tabs>
    <img src="static/img/icons/android-chrome-200x200_LFP.png" style="position:absolute; top:15px;height:40px;width:40px;">
    <div class="fabpush" style="display:table-cell;position:absolute; top:15px;right:0px;">
    <img class="fabimage" src="static/img/clubs/aj-auxerre.svg" alt="Push Notification" style="height:40px;width:40px;">
    </div>
    <div class="md-layout" style="background-color:#FFF59D" id="connexionBar" ref="connexionBar" v-show="connexionDown">
      <div class="md-layout-item"><md-icon class="md-primary">warning</md-icon></div>
      <div class="md-layout-item" md-vertical-align="center">Vous Ãªtes hors connexion ...</div>
    </div>

    <div class="md-layout" style="background-color:#FFF59D" id="serverBar" ref="serverBar" v-show="serverDown">
      <div class="md-layout-item"><md-icon class="md-primary">warning</md-icon></div>
      <div class="md-layout-item" md-vertical-align="center">Serveur injoignable ...</div>
    </div>
    <main>
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
let connexionDown = false
let serverDown = false
var debounce = require('lodash.debounce')

export default {
  name: 'app',

  methods: {
    toLigue1 () {
      this.$localStorage.set('ligue', 'ligue1')
    },
    toLigue2 () {
      this.$localStorage.set('ligue', 'ligue2')
    },
    showConnexionBar (event) {
      this.$localStorage.set('online', false)
      this.connexionDown = true
      window.scrollTo(0, 0)
    },
    hideConnexionBar (event) {
      this.$localStorage.set('online', true)
      this.connexionDown = false
    },
    showServerBar () {
      this.serverDown = true
      window.scrollTo(0, 0)
    },
    hideServerBar () {
      this.serverDown = false
    },
    updateClassement () {
      this.$http.get(process.env.CLASSEMENT_JSON).then(response => {
        // json success call back
        // console.log('success call data', response.data)
        this.classements = response.data
        this.$store.commit('setClassements', response.data)
        this.$localStorage.set('classements', JSON.stringify(response.data))
      }, response => {
        // error callback
        if (this.$localStorage.get('classements') !== null) {
          this.$store.commit('setClassements', JSON.parse(this.$localStorage.get('classements')))
        }
        console.log('error call data')
      })
    },
    updateMatcheApp () {
      this.$http.get(process.env.MATCHS_JSON).then(response => {
      // this.$http.get('static/data/NewMatches.json').then(response => {
        // json success call back
        // console.log('success call data', response.data)
        this.matchesApp = response.data
        this.$store.commit('setMatches', response.data)
        this.$localStorage.set('matches', JSON.stringify(response.data))
        this.notifications = response.data
      }, response => {
        // error callback
        if (this.$localStorage.get('matches') !== null) {
          this.$store.commit('setMatches', JSON.parse(this.$localStorage.get('matches')))
        }
        console.log('error call data')
      })
    },
    updateClassements: debounce(function () {
      this.updateClassement()
    }, 10000),
    updateMatchesApp: debounce(function () {
      this.updateMatcheApp()
    }, 10000),
    checkNotif: debounce(function () {
      for (var key in this.notifications.events) {
        var item = this.notifications.events[key]
        var myArray = []
        if (this.$store.ids !== null) {
          myArray = this.$store.state.ids
        }
        if (myArray.includes(item.id) !== true) {
          this.showPush(item, myArray)
        }
      }
    }, 1000),
    showPush (item, ids) {
      this.$store.commit('pushIDs', item.id)
      if (item.type === 'But') {
        this.showBut({title: item.type, message: item.club + ': ' + item.joueur})
      }
      if (item.type === 'CJn') {
        this.showCartonJaune({title: 'Carte Jaune', message: item.club + ': ' + item.joueur})
      }
      if (item.type === 'CRg') {
        this.showCartonRouge({title: 'Carte Rouge', message: item.club + ': ' + item.joueur})
      }
      if (item.type === 'Pen') {
        this.showPen({title: item.type, message: item.club + ': ' + item.joueur})
      }
    }
  },
  data () {
    return {
      connexionDown,
      serverDown,
      notifications: [],
      notification: '',
      matchesApp: '',
      classements: ''
    }
  },
  created: function () {
    this.updateMatcheApp()
    this.updateClassement()
    this.checkNotif()
  },

  mounted () {
    console.log('app.vue mounted')
    window.addEventListener('online', this.hideConnexionBar)
    window.addEventListener('offline', this.showConnexionBar)
    window.addEventListener('online', this.checkNotif)
    window.addEventListener('online', this.updateMatchesApp)
    window.addEventListener('online', this.updateClassements)
    if (!navigator.onLine) this.showConnexionBar()
  },
  watch: {
    notifications: function () {
      this.checkNotif()
    },
    notif: function () {
      self.addEventListener('notificationclick', event => {
        const notification = event.notification
        const action = event.action
        const link = notification.data.link
        if (action !== 'close') {
          if (link) {
            this.openWindow(link)
          }
        }
        notification.close()
        console.log('notificationclick action is', action)
      })
    },
    matchesApp: function () {
      this.updateMatchesApp()
    },
    classements: function () {
      this.updateClassements()
    }
  },

  notifications: {
    showBut: {
      type: 'success',
      timeout: 10000
    },
    showCartonJaune: {
      type: 'warn',
      timeout: 10000
    },
    showCartonRouge: {
      type: 'error',
      timeout: 10000
    },
    showPen: {
      type: 'info',
      timeout: 10000
    }
  }
}
</script>

<style>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

main {
  text-align: center;
  margin-top: 0px;
}

.complete-example {
  height: 540px;
  display: flex;
  flex-flow: column;
  position: relative;
  overflow: hidden;
  z-index: 1;

  .main {
    overflow: auto;
  }
}

h2 {
  padding-left: 50px;
}

md-toolbar>md-tabs{
  height: auto;
  width: auto;
}
.md-tabs + .md-tabs {
  margin-top: 24px;
}
a:not(.md-button):hover {
  text-decoration: none !important;
}
.tabs__item:not(.tabs__item--active) {
  opacity: 1 !important;
}
.tabs_slider{
  height:2px;
  width: 100% !important;
}
</style>



