<template>
<v-touch v-on:swipeleft="onSwipeLeft"  v-on:swiperight="onSwipeRight" style="min-height:700px;">
  <div>
    <span v-if="ligue === 'ligue1' && classement1s.live === '1'" style="width:5%;position:relative;float:left;color:red;">
      LIVE
    </span>
    <span v-if="ligue === 'ligue2' && classement2s.live === '1'" style="width:5%;position:relative;float:left;color:red;">
      LIVE
    </span>
    <span class="updated" v-if="Classements.updated">MAJ:{{Classements.updated.substring(11, 13)}}h{{Classements.updated.substring(14, 16)}}</span>
    <br>
    <div class="classements" style="width:100%;">
      <div class="classement-item" style="min-height:1px;">
        <div class="position" style="width:5%;display:table-cell;visibility:hidden;">
          LIVE
        </div>
        <div style="width:5%;display:table-cell;visibility:hidden;">
          <img src="static/img/clubs/ac-ajaccio.svg" class="logo_classement">
        </div>
        <div style="width:30%;display:table-cell;visibility:hidden;">
          club
        </div>
        <div style="width:10%;display:table-cell;position:relative;right:2px;">
          Pts
        </div>
        <div style="width:10%;display:table-cell;">
          J
        </div>
        <div style="width:10%;display:table-cell;">
          Diff
        </div>
      </div>
      <div class="classement-item" v-if="ligue === 'ligue1' && classement1s.classement !==null" v-for="classement in classement1s.classement">
        <div class="position" style="width:5%;display:table-cell;vertical-align:middle;">
          {{classement.position}}
        </div>
        <div style="width:5%;display:table-cell;">
          <img v-bind:src="getLogoClub(classement.slug)" class="logo_classement">
        </div>
        <div style="width:30%;display:table-cell;text-align:center; vertical-align:middle;">
          {{classement.club}}
        </div>
        <div style="width:10%;display:table-cell;font-size: calc(12px + 0.6vw + 0.4vh);">
          {{classement.points}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.joues}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.diff}}
        </div>
      </div>

      <div class="classement-item" v-if="ligue === 'ligue2' && classement2s.classement !==null" v-for="classement in classement2s.classement">
        <div class="position" style="width:5%;display:table-cell;">
          {{classement.position}}
        </div>
        <div style="width:5%;display:table-cell;">
          <img v-bind:src="getLogoClub(classement.slug)" class="logo_classement">
        </div>
        <div style="width:30%;display:table-cell;text-align:center; vertical-align:middle;">
          {{classement.club}}
        </div>
        <div style="width:10%;display:table-cell;font-size: calc(12px + 0.6vw + 0.4vh);">
          {{classement.points}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.joues}}
        </div>
        <div style="width:10%;display:table-cell;">
          {{classement.diff}}
        </div>
      </div>
    </div>
  </div>
</v-touch>
</template>

<script>
var debounce = require('lodash.debounce')
export default {
  name: 'classements',

  data () {
    return {
      Classements: '',
      classement1s: [],
      classement2s: [],
      classement: '',
      ligue: ''
    }
  },
  methods: {
    onSwipeRight () {
      if (this.ligue === 'ligue1') {
        this.$router.push('/')
      } else if (this.ligue === 'ligue2') {
        this.$localStorage.set('ligue', 'ligue1')
        this.$router.push('/ligue1')
      }
    },
    onSwipeLeft () {
      if (this.ligue === 'ligue1') {
        this.$localStorage.set('ligue', 'ligue2')
        this.$router.push('/ligue2')
      } else if (this.ligue === 'ligue2') {
        this.$router.push('/')
      }
    },
    getLogoClub (value) {
      return 'static/img/clubs/' + value + '.svg'
    },
    update () {
      this.$http.get(process.env.CLASSEMENT_JSON).then(response => {
        // json success call back
        // console.log('success call data', response.data)
        this.ligue = this.$store.state.ligue
        this.ligue = this.$localStorage.get('ligue')
        this.Classements = response.data
        this.classement1s = this.Classements.ligue1
        this.classement2s = this.Classements.ligue2
        this.$localStorage.set('classements', JSON.stringify(response.data))
      }, response => {
        // error callback
        if (this.$localStorage.get('classements') !== null) {
          this.ligue = this.$store.state.ligue
          this.ligue = this.$localStorage.get('ligue')
          this.Classements = JSON.parse(this.$localStorage.get('classements'))
          this.classement1s = this.Classements.ligue1
          this.classement2s = this.Classements.ligue2
        }
        console.log('error call data')
      })
    },
    // update () {
    //   this.ligue = this.$store.state.ligue
    //   this.ligue = this.$localStorage.get('ligue')
    //   this.Classements = this.$store.state.classements
    //   this.classement1s = this.Classements.ligue1
    //   this.classement2s = this.Classements.ligue2
    // },
    updateclassements: debounce(function () {
      this.update()
    }, 1000, true)
  },

  watch: {
    Classements: function () {
      this.updateclassements()
    },
    '$route' (to, from) {
      this.update()
    }
  },

  created: function () {
    window.addEventListener('online', this.updateclassements)
  },

  mounted: function () {
    this.update()
  }
}
</script>

<style>
.classements{
  font-size: calc(10px + 0.6vw + 0.4vh);
  font-weight: 600;
}
.classement-head{
  text-align: center;
  vertical-align: middle;
  min-height: 10px;
}
.classement-item{
  text-align: center;
  vertical-align: middle;
  min-height: 30px;
  margin-bottom:2px;
}
.updated{
  float: right;
  position: relative;
}
.logo_classement{
  height: calc(20px + 6vw);
  width: calc(20px + 6vw);
  padding: 0;
  max-width: 80px;
  max-height: 80px;
}
.classement-item:nth-child(even){
  background-color: #E0E0E0;
}
.classement-item:nth-child(even)>.position{
  background-color:#CFD8DC;
}
</style>



