import Vue from 'vue'
import Router from 'vue-router'
import Matchs from '@/components/Matchs'
import Classements from '@/components/Classements'
import Commentaires from '@/components/Commentaires'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Matchs',
      component: Matchs
    },
    {
      path: '/classements',
      name: 'Classements',
      component: Classements
    },
    {
      path: '/ligue1',
      name: 'Ligue1',
      component: Classements
    },
    {
      path: '/ligue2',
      name: 'Ligue2',
      component: Classements
    },
    {
      path: '/commentaires',
      name: 'Commentaires',
      component: Commentaires
    }
  ]
})



