// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueMaterial from 'vue-material'
import VueResource from 'vue-resource'
import VueLocalStorage from 'vue-localstorage'
import 'vue-material/dist/vue-material.css'
import Vuetify from 'vuetify'
import VueNotifications from 'vue-notifications'
import miniToastr from 'mini-toastr'
import Vuex from 'vuex'

Vue.config.productionTip = false

Vue.use(VueResource)
Vue.use(VueMaterial)
Vue.use(VueLocalStorage)
Vue.use(Vuetify)
Vue.use(Vuex)

// NOTIFICATIONS - start

// If using mini-toastr, provide additional configuration
const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

// Binding for methods .success(), .error() and etc. You can specify and map your own methods here.
// Required to pipe our output to UI library (mini-toastr in example here)
// All not-specified events (types) would be piped to output in console.
const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

// Activate plugin
Vue.use(VueNotifications, options)

// NOTIFICATIONS - end

// vuex store
const store = new Vuex.Store({
  state: {
    matches: [],
    classements: [],
    ids: []
  },
  mutations: {
    setMatches (state, data) {
      state.matches = data
    },
    setClassements (state, data) {
      state.classements = data
    },
    pushIDs (state, data) {
      state.ids.push(data)
    }
  }
})

var VueTouch = require('vue-touch')
Vue.use(VueTouch, {name: 'v-touch'})
VueTouch.config.swipe = {
  direction: 'horizontal'
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



