<template>
<v-touch v-on:swipeleft="onSwipeLeft"  v-on:swiperight="onSwipeRight" style="min-height:700px;">
  <div>
    <span class="updated" v-if="Matches.updated">MAJ:{{Matches.updated.substring(11, 13)}}h{{Matches.updated.substring(14, 16)}}
    </span>
    <div v-if="Matches.matches">
      <md-toolbar class="match_Live" v-if="match.statut == 'L'" v-for="match in Matches.matches" :key="match.id" style="">
        <div style="width:100%;height:100%;" @click=toComment(match.id)>
          <img v-bind:src="getLogoClub(match.recevant.slug)" class="logo">
          <span class="score" style="color:#ff1e00;">{{match.recevant.score}}</span>
          <span class="time" style="color:#ff1e00;">{{match.time}}</span>
          <span class="score" style="color:#ff1e00;">{{match.visiteur.score}}</span>
          <img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo">
          <div style="width:100%;"><progress max="100" style="width:40px;position:relative;bottom:25px;height:8px;"></progress></div>
          <div class="ligue">Ligue{{match.ligue}}</div>  
        </div>
      </md-toolbar>
      <br> 
      <md-toolbar class="match" v-if="match.statut == 'P'" v-for="match in Matches.matches" :key="match.id">
        <div style="width:100%;height:100%;" @click=toComment(match.id)>
            <img v-bind:src="getLogoClub(match.recevant.slug)" class="logo">
            <span class="temp">{{match.time}}</span>
            <img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo">
            <div class="ligue">Ligue{{match.ligue}}</div>     
        </div>
      </md-toolbar> 
      <br> 
      <md-toolbar class="match" v-if="match.statut == 'J'" v-for="match in Matches.matches" :key="match.id">
       <div style="width:100%;height:100%;" @click=toComment(match.id)>
          <img v-bind:src="getLogoClub(match.recevant.slug)" class="logo_joue">
          <span class="score">{{match.recevant.score}}</span>
          <span class="time">-</span>
          <span class="score">{{match.visiteur.score}}</span>
          <img v-bind:src="getLogoClub(match.visiteur.slug)" class="logo_joue">
          <div class="ligue">Ligue{{match.ligue}}</div>     
        </div>
      </md-toolbar>
      <span>17h02</span>
    </div>
    <div v-else style="margin-left:10%;font-weight:bold;font-size:20px;position:relative;top:100px;">
      Aucun match aujourdâhui 
    </div>     
  </div>
</v-touch>
</template>

<script>
var debounce = require('lodash.debounce')

export default {
  name: 'matchs',

  data () {
    return {
      Matches: [],
      match: '',
      id: ''
    }
  },

  methods: {
    onSwipeLeft () {
      this.$localStorage.set('ligue', 'ligue1')
      this.$router.push('ligue1')
    },
    onSwipeRight () {
      this.$localStorage.set('ligue', 'ligue2')
      this.$router.push('ligue2')
    },
    toComment (id) {
      this.$localStorage.set('id', id)
      this.$router.push('Commentaires')
    },
    getLogoClub (value) {
      return 'static/img/clubs/' + value + '.svg'
    },
    update () {
      this.$http.get(process.env.MATCHS_JSON).then(response => {
        // json success call back
        // console.log('success call data', response.data)
        this.Matches = response.data
        this.$localStorage.set('matches', JSON.stringify(response.data))
      }, response => {
        // error callback
        if (this.$localStorage.get('matches') !== null) {
          this.Matches = JSON.parse(this.$localStorage.get('matches'))
        }
        console.log('error call data')
      })
    },
    // update () {
    //   this.Matches = this.$store.state.matches
    // },
    updateMatches: debounce(function () {
      this.update()
    }, 1000)
  },

  watch: {
    Matches: function () {
      this.updateMatches()
    }
  },
  created: function () {
    window.addEventListener('online', this.updateMatches)
  },

  mounted: function () {
    this.update()
  }
}
</script>

<style>
  html, body {
    width:100%;
    height: 100%;
    margin:0;
    padding:0;
  }
  .updated{
    float: right;
    position: relative;
  }
  .match {
    padding:0;
  }
  .match_Live {
    padding:0;
  }
  .score{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(25px + 0.6vw + 0.4vh); 
    padding: 5px 0;
    padding:0 30px;
    vertical-align:middle;
  }
  .time{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(12px + 0.6vw + 0.4vh); 
    padding: 5px 0;
    text-align: center;
  }
  .temp{
    margin: 5px auto;
    font-weight: bold;
    font-size: calc(15px + 0.6vw + 0.4vh); 
    padding: 5px 64px;
    text-align: center;
  }
  .logo{
    position: relative;
    top: 5px;
    height: calc(40px + 6vw);
    width: calc(40px + 6vw);
    padding: 0;
    max-width: 80px;
    max-height: 80px;
  }
  .logo_joue{
    position: relative;
    top: 5px;
    height: calc(40px + 6vw);
    width: calc(40px + 6vw);
    margin: 0 8px;
    max-width: 80px;
    max-height: 80px;
  }
  .ligue{
    position: relative;
    bottom: 20px;
    font-size: calc(9px + 0.6vw);
  }

  .classement{
    font-size: calc(5px + 1vw);
  }
  .carton{
    font-size: calc(7px + 1vw);
  }
</style>



